




import ebus from '@/inserts/eventBus';
import { mapGetters, mapActions } from 'vuex';

export default {
  mounted() {
    ebus.$on("msg", (data) => {
      console.log(data);
    })
    this.inputer = this.getInputer;
    this.daysGrouping();
    let analyScript = (p) => {
      let canvas, timeVal, value, arr, ys, part = [], data = [];
      this.offset = 20;
      let offset = this.offset;

      // S E T U P
      p.setup = () => {
        canvas = p.createCanvas(p.windowWidth, p.windowHeight);
        canvas.parent("analy-sales-progress");
        canvas.id("analy-sales-canvas");
        canvas.class("analy-sales-canvas");
        // canvas.canvas.style.width = "100%";
        p.smooth();

        for (let i = 0; i < 10; i++) {
          part.push(new particle(p.random(0, p.width), p.random(0, p.height), p.random(8, 12)));
        }
        setStock(this);
        // console.log(part);
      }//  END - S E T U P

      // D R A W
      p.draw = () => {
        let lineX = p.width - (offset-16);
        let lineY = p.height - (offset-12);
        let offX = 0, off2 = 3, length = 4;
        // ------------------------------------------------------------------

        ys = [...this.anaData];
        
        setStock(this);
        p.background("#e3eefc");
        for (let i = 0; i < part.length; i++) {
          const parti = part[i];
          parti.move(true);
        }

        // GROUP BY DATE - USING THE DAY
        let daysGroup = this.daysGroup && Object.entries(this.daysGroup);
        daysGroup.sort((b,a) => +a[0] < +b[0] ? 1 : -1);
        // console.log(daysGroup);
        p.fill(p.color(33, 140, 54, 120));
        p.ellipse(lineX-(offset-off2), lineY, 10,10);
        
        let arrMax = [];
        daysGroup.forEach(v => {
          let vv = v[1] as Array<any>;
          arrMax.push(vv[vv.length - 1])
        });
        let max = [...arrMax].sort((b,a) => +a > +b ? 1 : -1)[0];
        let xPositioning = offset;
        daysGroup.forEach((v,i,a) => {
          let vArr = v[1] as Array<any>;
          let heightRatio = +vArr[vArr.length-1] / max;
          let fullWidth = ((lineX-offX)-offset-16);
          let fullHeight = (lineY-offset-off2);
          let heighting = fullHeight * (heightRatio);
          let height = (heighting-off2)-(heighting-offset-off2) + fullHeight * (1-heightRatio);
          // --------------------------------------------------
          p.fill(p.color(33, 140, 54, 35));
          let padding = 3;
          let paddingGap = (fullWidth/a.length) - ((fullWidth/a.length) - padding);
          let paddingMove = (paddingGap / (a.length-1));
          // if(i == 0) paddingMove = 0;
          if(i != 0) xPositioning += ((fullWidth/a.length)) + paddingMove;
          
          p.rect(xPositioning, height, (fullWidth/a.length) - padding, heighting);
          p.textSize(18);
          p.fill(p.color(0, 80))
          p.text(new Date(+v[0]).getDate().toString(), xPositioning, height);

        });
        
        
        p.noFill();
        p.stroke(p.color("#e55c84"));
        p.strokeWeight(0.6);

        p.beginShape();
        this.anaData.forEach((v,i,data) => {
          if(i == 0) p.vertex(v.x, v.y);
          if(i != 0 || i != data.length-1) createBezier(v.x, v.y, 10, true);
          else createBezier(v.x, v.y, 10);
        });
        // createBezier(p.width/2, 10, 40, true);
        // createBezier(p.width, p.height, 40);
        p.endShape();
        // CREATE CIRCLES AS POINTS & TEXT...
        this.anaData.forEach((v,i,data) => {
          p.fill(p.color(255));
          p.stroke(p.color("#e55c84"));
          p.strokeWeight(0.3);
          p.ellipse(v.x, v.y, 13);
          p.strokeWeight(1.2);
          p.ellipse(v.x, v.y, 8);

          p.noStroke();
          p.fill(p.color("#212A36"));
          p.textSize(9);
          p.textAlign(p.CENTER);
          p.text(v.value, v.x, v.y-9);
          
        });
        ys && ys.sort((b,a) => a.value > b.value ? 1 : -1);

        // console.log([...this.anaData], ys[0], ys[ys.length-1]);
        // ys && ys.length 
        p.stroke(p.color(0));
        p.strokeWeight(0.1);
        p.stroke(p.color("#3e80d6"));

        // CREATE VERTICAL & HORIZONTAL LINES
        let ysTop = ys && ys.length ? (ys[0].y - (offset-10)) : 0;
        p.line(offset-offX, ysTop, offset-offX, lineY);
        p.line(offset-off2, lineY-off2, lineX, lineY-off2);

        // HORIZONTAL LINE POSITIONING
        this.anaData.forEach((v,i,a) => {
          p.strokeWeight(0.8);
          p.line(v.x, (lineY-off2)-(length/2), v.x, (lineY-off2)+(length/2));
        });

        // VERTICAL LINE POSITIONING
        let items = 5;
        for (let i = 0; i < items; i++) {
          let yTop = offset + ((((lineY-off2)-offset)/items) * i);
          // console.log(yTop);
          p.line(offset-(offX)-(length/2), yTop, (offset-offX)+(length/2), yTop);
          if(i==items-1) p.line(offset-(offX)-(length/2), lineY-off2, (offset-offX)+(length/2), lineY-off2);
        }
        
        // p.textSize(24);
        // p.fill(0);
        // p.text("this.getInputer", p.width/2, p.height/2);

        /* p.fill(255, 255, 255, 150);
        p.ellipse(0, p.height/2, 20, 20)
        p.ellipse(p.width, 0, 20, 20) */

        // p.textSize(24);
        // p.text(p.width.toString(), p.width/2, p.height/2);
      }//  END - D R A W

      // R E S I Z E D
      p.windowResized = () => {
        p.resizeCanvas(p.windowWidth, p.windowHeight);
        setStock(this);
      }//  END - R E S I Z E D

      // F U N C T I O N S
      function createBezier(x, y, offset=12, horizon=false) {
        if(horizon) p.bezierVertex(x-offset, y-offset, x, y, x+(offset), y+(offset*0.13));
        else p.bezierVertex(x-offset, y, x, y, x+offset, y);
      }
      function setStock(self) {
        self.setStockItems().then((stock) => {
          self.anaData = self.getStockItems.map((val,i,arr) => {
            let max = +[...arr].sort((b,a) => +a.quatity > +b.quatity ? 1 : -1)[0].quatity;
            return {
              x: offset + (i * ((p.width-offset*2) / (arr.length - 1))),
              y: offset + ((p.height-(offset*2)) - ((+val.quatity / max) * (p.height-(offset*2)))),
              value: +val.quatity
            };
          });
        }).catch((err) => {
          console.log(err);
        });
      }
      /* function createBezier(x, y, offset=12, horizon=false) {
        p.bezierVertex(x, y, x, y, x, y);
      } */
      // C L A S S E S
      class particle {
        x; y; size; time; initX; initY; seed;
        
        constructor(x=0, y=100, size=20, time=0) {
          this.x = x;
          this.y = y;
          this.initX = this.x;
          this.initY = this.y;
          this.size = size;
          this.time = time;
          this.seed = p.random(-38473, 28399);
          // this.draw();
        }
        draw(x=this.x, y=this.y) {
          p.fill(p.color(61, 75, 92, 30));
          p.noStroke();
          p.ellipse(x, y, this.size, this.size);
        }
        move(draw=false) {
          draw && this.draw();
          this.time ++;
          this.x = this.initX + (p.sin((this.time * 0.5)/50) * 15) + (p.noise(this.seed + ((this.time * 0.2)/50)) * 120);
          this.y = this.initY + (p.sin((this.time * 0.3)/100) * 10) + (p.noise(this.seed + 9535 + ((this.time * 0.3)/100)) * 100);

          // REFLECT THE DIRECTION
          if(this.x >= p.width-(this.size/2)) this.x = this.x - ((this.x - (p.width-(this.size/2))) * 2);
          if(this.y >= p.height-(this.size/2)) this.y = this.y - ((this.y - (p.height-(this.size/2))) * 2);
          if(this.x <= 0+ (this.size/2)) this.x = -(this.x - (this.size/2)) + this.size/2;
          if(this.y <= 0+ (this.size/2)) this.y = -(this.y - (this.size/2)) + this.size/2;
          // console.log(this.x);
        }
      }
    }
    new this.p5(analyScript);
  },
  data: () => ({
    anaData: [],
    offset: 0,
    daysGroup: [],
    inputer: ''
  }),
  computed: {
    ...mapGetters(['getStockItems', 'getInputer']),
    upd() {
      console.log("Updating");
      return this.getStockItems;
    }
  },
  methods: {
    ...mapActions(['setStockItems']),
    daysGrouping() {
      this.setStockItems().then((stock) => {
        this.daysGroup = stock.reduce((acc, cur) => {
          let d = new Date(+cur.createdon);
          let theDate = new Date(d.getFullYear(), d.getMonth(), d.getDate()).getTime();
          if (!acc[theDate]) acc[theDate] = [];
          acc[theDate].push(cur);
          return acc;
        }, {});
        let v = Object.entries(this.daysGroup);
        v.forEach((v,i,arr) => {
          let vv = v[1] as Array<any>;
          vv && vv.length ? this.daysGroup[v[0]].push(vv.reduce((acc, cur) => {
            return acc + +cur.quatity;
          }, 0)) : null;
        });
        console.log(this.daysGroup);
      }).catch((err) => {
        console.log(err);
      });
    }
  },
  watch: {
    getInputer(inputer) {
      console.log(inputer);
    }
  }
}
