































































import ebus from '@/inserts/eventBus';
import { mapActions, mapGetters } from 'vuex';

export default {
mounted() {
  // this.delIFExisting;
  ebus.$on("msg", (data) => {
    console.log("Msg:", data);
  })
  this.setStockItems().then((sales) => {
    this.stockItems = sales;
    this.groupedDate = sales.reduce((acc, cur) => {
      let d = new Date(+cur.createdon);
      let theDate = new Date(d.getFullYear(), d.getMonth(), d.getDate()).getTime();
      if (!acc[theDate]) acc[theDate] = [];
      acc[theDate].push(cur);
      return acc;
    }, {});
    
  }).catch((err) => {
    console.log(err);
  });

},
data: () => ({
  inputer: '',
  stockForm: {
    item: '',
    identity: '',
    quatity: 0,
    unitprice: 0,
    createdon: null,
    spCanvas: null
  },
  delStock: false,
  stockItems: [],
  groupedDate: null
}),
components: {
  canvaser: () => import("../inserts/p5s/analy1.vue")
},
computed: {
  ...mapGetters(['getStockItems']),
  delIFExisting() {
    console.log("delIFExisting");
    let stock = this.getStockItems;
    if(!stock) return;
    let v = [...stock].map(v => v = v.identity).join("+=");
    let pattern = new RegExp("\\b("+this.stockForm.identity+")\\b", "igm");
    console.log(v, v.search(pattern));

    let vStockSelected = this.stockItems.filter(v => v.identity == this.stockForm.identity);
    if(this.stockForm.identity != "" && v.search(pattern) >= 0) {
      this.delStock = true;
      console.log();
      if(vStockSelected && vStockSelected.length) {
        let v = vStockSelected[0];
        this.stockForm.item = v.item;
        this.stockForm.quatity = v.quatity;
        this.stockForm.unitprice = v.unitprice;
      }else {
        this.stockForm.item = "d";
        this.stockForm.quatity = "";
        this.stockForm.unitprice = "";
      }
    }
    else this.delStock = false;
  }
},
methods: {
  ...mapActions(['setStockItems','returnSales','delStockItems', 'setInputer']),
  async addUpdate(e:Event) {
    console.log(e);
    let sendData = this.stockForm;
    if(!this.delStock) {
      if(sendData.item != "" && sendData.identity != "" && +sendData.quatity > 0 && +sendData.unitprice > 0) {
        // IF THE SEND-DATA IS NOT EMPTY, INSERT INTO THE STOCKS
        sendData.createdon = new Date(/* 2019, 8, 3, 13,3 */).getTime();
        this.setStockItems(sendData).then((stock) => {
          console.log("Stock", stock);
          this.stockItems = this.getStockItems;
        }).catch((err) => {
          console.log("Set Stock", err);
        });
      }else {
        console.log("Only Got the external Data: ", await this.setStockItems());
      }
    }else {
      // UPDATE THE STOCK RATHER.......

    }

  },
  deleteStock(e:Event) {
    if(this.delStock) {
      // DELETE AN ITEM FROM STOCK
      this.delStockItems({
        identity: this.stockForm.identity
      }).then((stock) => {
        this.stockItems = this.getStockItems;
      }).catch((err) => {
        console.log(err);
      });
    }
  }
},
watch: {
  '$route.name'(name) {
    this.setStockItems().then((sales) => {
    this.stockItems = sales;
    console.log("Manage: ",sales);
  }).catch((err) => {
    
  });  
  },
  '$route.path'(path) {
    console.log(path);
  },
  inputer(inputer) {
    ebus.$emit("msg", {
      ready: "Peace"
    });
    this.setInputer(inputer);
  }
},
}
