import Vue from 'vue';
import Vuex from 'vuex';

import Nedb from 'nedb';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    salesItems: null,
    stockItems: null,
    dbstock: new Nedb({filename: "a-stock.db", autoload: true}),
    dbsales: new Nedb({filename: "a-sales.db", autoload: true}),
    inputer: '',
    manipHost: 'http://localhost:8000/image'
  },
  getters: {
    getSalesItems(state) {
      return state.salesItems;
    },
    getStockItems(state) {
      return state.stockItems;
    },
    getInputer(state) {
      return state.inputer;
    },
    getManipHost(state) {
      return state.manipHost;
    }
  },
  mutations: {
    SETSTOCKITEMS(state, payload) {
      state.stockItems = payload;
    },
    INSERTSALES(state, payload) {
      state.salesItems = payload;
    },
    RETURNSALES() {

    },
    SETINPUTER(state, payload) {
      state.inputer = payload;
    }
  },
  actions: {
    setStockItems({state,commit}, payload=null) {
      // state.dbstock.remove({$where: function () { return this.quatity == 51}}, {multi: true}, () => {})
      return new Promise((resolve, reject) => {
        // INSERT INTO STOCK    *********************** 
        if(!payload) {
          state.dbstock.find({}, (err, docs) => {
            if(err) reject("Data is empty!");
            else {
              commit("SETSTOCKITEMS", docs);
              resolve(docs);
            }
          });
          
        }else {
          state.dbstock.insert(payload, (err, newDoc) => {
            if(err) reject(err);
            else {
              // IF INSERT IS SUCCESSFUL, RETURN THE NEWDOC & ALL DOCS
              state.dbstock.find({}, (err, docs) => {
                if(err) reject(err);
                else {
                  commit("SETSTOCKITEMS", docs);
                  resolve([newDoc, docs]);
                }
              });
            }
          });
        }

        
      });
    },
    insertSales({state,commit}, payload = null) {
      return new Promise((resolve, reject) => {
        if(!payload) reject("Sales Items is empty!")
        else {
          state.dbsales.insert(payload, (err, newDocs) => {
            if(err) reject(["Insert Didn't work as expected", err]);
            else {
              resolve(newDocs);
              commit("INSERTSALES", newDocs);
            }
          });
        }
      });
    },
    returnSales({state,commit}, payload = null) {
      return new Promise((resolve, reject) => {
        if(payload) {
          console.log("docs");
          
        }else {
          state.dbsales.find({}, (err, docs) => {
            if(err) reject(err);
            else {
              resolve(docs);
              commit("RETURNSALES");
            }
          });
        }
      });
    },
    delStockItems({state,commit}, payload = null) {
      // AN OBJECT MUST BE GIVEN AS THE PAYLOAD
      
      return new Promise((resolve, reject) => {
        if(payload) {
          let arrData = Object.entries(payload);
          // return;
          arrData.forEach((v,i,a) => {
            state.dbstock.remove({$where: function() {return this[arrData[i][0]] == arrData[i][1]}}, {multi: true}, (err, numRemoved) => {
              console.log("NumRemoved:", numRemoved);
            });
          })
          state.dbstock.find({}, (err,docs) => {
            if(err) reject(err);
            else {
              commit("SETSTOCKITEMS", docs);
              resolve(docs);
            }
          })
        }
      });
    },
    setInputer({commit}, payload = null) {
      commit("SETINPUTER",payload);
    }
  },
});
